.list-row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.5rem;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 0.5rem;
    padding: 0.5rem;
    border-radius: 1rem;
}

.list-row:hover {
    background-color: var(--darkgrey);
    transition: all 0.3s;
}

.list-row td {
    width: 18%;
}

.td-img {
    width: min-content !important;
}

.mobile-td-cap,
.mobile-price {
    display: none;
}

@media only screen and (max-width: 1000px) {
    .td-img img {
        width: 2rem !important;
        height: 2rem !important;
    }

    .price-chip-list {
        min-width: 2rem !important;
        font-size: 0.6rem !important;
        border-width: 2px !important;
        padding: 0.25rem 0.5rem !important;
    }

    .trending-icon-list {
        display: none !important;
    }
}

@media only screen and (max-width: 800px) {
    .td-img {
        margin-right: 0.5rem;
    }

    .list-row {
        margin-top: 1rem;
        width: 100%;
        gap: 0rem !important;
        padding: 0rem;
    }

    .td-mkt-cap,
    .desktop-price {
        display: none !important;
    }

    .mobile-td-cap,
    .mobile-price {
        display: block;
    }

    .coin-price-list {
        font-size: 0.8rem !important;
        margin-right: 0.5rem;
    }

    .coin-symbol-list,
    .coin-name-list,
    .coin-total_volume-list {
        font-size: 80% !important;
    }
}

@media only screen and (max-width: 400px) {
    .td-price-chip-list {
        display: none;
    }
}